<template>
  <div class="root">

    <div>
      <!-- 二级路由 -->
      <keep-alive>
        <router-view/>
      </keep-alive>
    </div>

    <van-tabbar v-model="tabbarName" active-color="#FE9702" size="34">
      <van-tabbar-item :name="item.name" :icon="item.icon" v-for="(item, index) in tabbarData" :key="index" @click="changePage({name: item.name})">{{item.title}}</van-tabbar-item>
    </van-tabbar>
  </div>
</template>

<script>

export default {
  name: 'root',
  data() {
    return {
      tabbarName: 'home',
      tabbarData: [
        {
          name: 'home',
          title: '首页',
          icon: 'wap-home-o'
        },
        {
          name: 'classify',
          title: '分类',
          icon: 'apps-o'
        },
        {
          name: 'shopcart',
          title: '购物车',
          icon: 'shopping-cart-o'
        },
        {
          name: 'mine',
          title: '我的',
          icon: 'contact'
        }
      ]
    }
  },

  created() {
    // 纠正底部栏激活状态
    this.tabbarName = this.$route.name;
  },

  methods: {
    changePage(o) {
      //如果是相同路由，直接拦截
      if(this.$route.name == o.name){
        return;
      }
      this.$router.push(o);
    }
  }
}
</script>

<style lang="less" scoped>
  .root{
    .van-tabbar-item{
      font-size: 14px;
    }
    /deep/.van-icon, .van-icon::before{
      font-size: 24px;
    }
  }
</style>
